.App {
  max-width: 100%;
  max-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  padding: 10px;
}

.App h1 {
  color: white;
  text-align: center;
}

.App > div {
  aspect-ratio: 1 / 1;
  width: 100%;
  max-height: 100vh;
  display: grid;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows:    repeat(5, 20%);
  padding: 20px;
}

.App button {
  all: unset;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: white;
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  user-select: none;
  padding: 5px;
  white-space: break-spaces;
  transition-duration: 0.1s;
}

.App button:focus {
  text-decoration: underline;
}

.App button.checked {
  background-color: #f280a1;
}

.App > span {
  color: white;
}